const Index = () => import(/* webpackChunkName: "group-home" */ '@/views/my-blog/index')
const Homepage = () => import(/* webpackChunkName: "group-home" */ '@/views/my-blog/homepage')

const router = {
  path:'/',
  name:'Index',
  component:Index,
  children: [
    {path: '/',
      name: 'Homepage',
      component: Homepage
    }
  ]
}

export default router




