const webIndex = () => import(/* webpackChunkName: "group-home" */ '@/views/wedding/index')
const Homepage = () => import(/* webpackChunkName: "group-home" */ '@/views/wedding/homepage')

const router = {
  path:'/webIndex',
  name:'Index',
  component:webIndex,
  children: [
    {path: '/homepage',
      name: 'Homepage',
      component: Homepage
    }
  ]
}

export default router




